import React,{useState, useContext} from 'react';

import {useCookies} from 'react-cookie'

import url from '../connection';

const Card = (props)=>{

  const [good,setGood] = useState(props.good);
  const [cookies, setCookie] = useCookies(['goodList']);

  const onGood = () =>{
    if(good){
      const data = {
        playlist_id: props.data.playlist_id
      };
      fetch(url+'ungood',{
        method: 'POST', // or 'PUT'
        body: JSON.stringify(data), // data can be `string` or {object}!
        headers:{
          'Content-Type': 'application/json'
        }
      })
      .then(function(response) {
        return response.json();
      })
      .then(function(myJson) {
        setGood(false);
        cookies.goodList.splice(cookies.goodList.indexOf(props.data.playlist_id),1);
        setCookie('goodList',cookies.goodList);
      });
    }else{
      const data = {
        playlist_id: props.data.playlist_id
      };
      fetch(url+'good',{
        method: 'POST', // or 'PUT'
        body: JSON.stringify(data), // data can be `string` or {object}!
        headers:{
          'Content-Type': 'application/json'
        }
      })
      .then(function(response) {
        return response.json();
      })
      .then(function(myJson) {
        setGood(true);
        setCookie('goodList',cookies.goodList.concat([props.data.playlist_id]),{path:'/'});
      });
    }
  }  
    return(
        <div className="card">
          <div className="angle">
            <img className="thumbnail" src={props.data.thumbnail} onClick={()=>{window.location.href= `https://www.youtube.com/playlist?list=${props.data.playlist_youtube_id}`}}/>
          </div>
          <div className="bar">
            <div className="decs">
              <div className="name">{props.data.name}</div>
              <div className="song">14 songs</div>
            </div>
            <div className="right">
              <div className={`button ${(good)? "good" : ""}`} onClick={onGood}>GOOD</div>
            </div>  
            </div>
        </div>
    )
}

export default Card;