import React, { useRef, createRef, useState, useEffect } from 'react';
import './App.css';

import Card from './Components/card'

import url from './connection';

import {useCookies} from 'react-cookie'

const key = "AIzaSyA89zQWGOtArcJWrnD0IVmjW65Z4BlkyWM"

const CARD_WIDTH = 680;

function App() {

  const scrollRef = createRef()
  const urlRef = createRef()

  const [scrollPostion,setscrollPostion]= useState(0);
  const [item,setItem] = useState([]);
  const [state,setState] = useState("none");

  const [cookies, setCookie, removeCookie] = useCookies(['goodList']);

  useEffect(()=>{
    fetch(url)
      .then(function(response) {
        return response.json();
      })
      .then(function(data) {
        console.log(data);
        if(!data.result){return;}
        setItem(item.concat(data.result));
      });
      if(!cookies.goodList){
        setCookie("goodList",[])
      }
  },[])

  const onUpload = ()=>{
    //change url 
    const data = {
      url:urlRef.current.value
    }
  
    fetch(url+'upload',{
      method: 'POST', // or 'PUT'
      body: JSON.stringify(data), // data can be `string` or {object}!
      headers:{
        'Content-Type': 'application/json'
      }
    })
      .then(function(response) {
        return response.json();
      })
      .then(function(myJson) {
        console.log(JSON.stringify(myJson));
        window.location.href = "/";
      });
  }

  return (
    <div className="App" onWheel={(element)=>{
      let page = Math.floor(item.length/10);

      const offset = scrollPostion-element.deltaY/2;

      if(item.length*CARD_WIDTH - window.innerWidth < 0){return;}

      if(offset <= 0 && item.length*CARD_WIDTH - window.innerWidth >= -offset){
        setscrollPostion(offset);
      }else if(offset >= 0){
        setscrollPostion(0);
      }else{
        setscrollPostion(-item.length*CARD_WIDTH + window.innerWidth);
      }

      if(state=="none" && -scrollPostion-10*CARD_WIDTH*page>5*CARD_WIDTH){
        setState("loading");

        fetch(url+`/?page=${page+1}`,{
          method: 'GET', // or 'PUT'// data can be `string` or {object}!
          headers:{
            'Content-Type': 'application/json'
          }
        })
          .then(function(response) {
            return response.json();
          })
          .then(function(data) {
            if(data.result.length==0){
              setState("end");
              return;
            }
            setState("none");
            setItem(item.concat(data.result));
          });
      }
    }}>
      <div className="backboard"><div className="band"></div></div>
      <div className="Title"> YOU PICK </div>
      
      <div ref={scrollRef} style={{transform:`translate(${scrollPostion}px,0px)`}} className="list">
        {
          item.map((data,index)=>{
            let good = false;
            if(cookies.goodList.indexOf(data.playlist_id)!=-1){
              good = true;
            }
            return(<Card key={index}data={data} good={good}/>)
        })
        }
      </div>
      <div className="addPlayList">
          <div className="medium">url</div><input ref={urlRef} className="input_url input_box"></input>
          <div className="sumbit" onClick={onUpload}>UPLOAD</div>
      </div>
    </div>
  );
}

export default App;
